import React, { Component } from 'react';
import './Home.css';
import mathe from '../../Images/math.jpg'
// import hommeordi from '../../Images/hommeordi.jpg'
import ecriture from '../../Images/ecriture.jpg'

class Home extends Component {
    render() {
        return (
            <div>
                <Carrousel />
                <Txt />
            </div>

        );
    }
} export default Home

export const Carrousel = () => {
    // mettre du code js ici si besoin
    return (
        <div className="col-10 offset-1 " id="caroustyle">
            <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                <ol className="carousel-indicators">
                    <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                </ol>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="d-block w-100" src={ecriture} height="300px" alt="Second slide"></img>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={mathe} height="300px" alt="First slide"></img>
                    </div>
                    <div className="carousel-item">
                        {/* <img class="d-block w-100" src={hommeordi} height="300px" alt="First slide"></img> */}
                        <img className="d-block w-100"  height="300px" alt="First slide"></img>
                    </div>
                </div>
            </div>
            <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
            </a>
        </div>
    )
}

export const Txt = () => {
    return (
        <div>
            <div className="row">
                <div className="col-10 offset-1" id="ligne">
                    <p className="pHome">Bienvenue sur Fe des maths ! Sur ce site vous trouverez des cours sous format vido avec de nombreux quizz dapplications ainsi que des exercices avec corrections. Bienvenue dans le monde merveilleux des mathmatiques ! Bonne visite et bons progrs !</p>
                </div>
            </div>
            <div className="row" id="data">
                <div className="col-4 offset-1" id="margeleft">
                    <p className="pHome">Non ! Vous ntes pas nul en maths ! Si vos rsultats vous font penser le contraire, beaucoup de professeurs laffirment : personne nest vraiment  nul  en mathmatiques. Si vous pensez toujours le contraire, il faut comprendre que cette matire nest faite que de logique. Et cest cette dernire que vous devez apprendre  dchiffrer. Avec un peu de travail sur Fe des maths, vous parviendrez srement  rhausser votre moyenne en maths !</p>
                </div>
                <div className="col-6" id="video">
                    <div className="row justify-content-center">
                        <iframe width="560" height="315" src="https://www.youtube.com/embed/wrNLKRX99Og" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                    </div>
                    <p className="pHome">Le cours de maths a t compliqu ? Vous ne lavez pas compris ou peu ? Cela arrive videmment. Et vous vous dtes que vous comprendrez mieux au prochain cours ? Cest possible, cependant la meilleure solution pour vous reste de creuser la question vous-mme en prenant le temps. Servez-vous de nos cours vidos et quizz ! Une fois le cours assimil, jetez-vous dans le bain avec les exercies et prenez le risque de faire des erreurs. Les exercices corrigs vous permettront de les identifier pour mieux les comprendre. Ceci est probablement lune des meilleures mthodes pour progresser en maths !</p>
                </div>
            </div>
        </div >
    )
}

